<template>
  <div class="row pt-4">
    <template v-for="post in posts">
      <div class="col-md-4" :key="post.link">
        <div class="card mb-4 box-shadow">
          <img
            class="card-img-top"
            :alt="shortenText(post.title, 0, 30)"
            style="height: 225px; width: 100%; display: block"
            :src="post.thumbnail"
            data-holder-rendered="true"
          />
          <div class="card-body">
            <p class="card-title">
              {{ shortenText(post.title, 0, 30) + "..." }}
            </p>
            <p class="card-text">
              {{ "..." + shortenText(toText(post.content), 60, 300) + "..." }}
            </p>
          </div>
          <div class="d-flex justify-content-between align-items-center">
            <div class="btn-group">{{ "@" + username }}</div>
            <small class="text-muted">{{
              shortenText(post.pubDate, 0, 10)
            }}</small>
          </div>
        </div>
      </div>
    </template>
  </div>
</template>


<script>
export default {
  name: "Medium",
  props: ["username"],
  data: function () {
    return {
      posts: {},
    };
  },
  beforeMount() {
    this.getDataFromApi();
  },
  methods: {
    getDataFromApi: async function () {
      await fetch(
        "https://api.rss2json.com/v1/api.json?rss_url=https://medium.com/feed/@" +
          this.username
      )
        .then((res) => res.json())
        .then((data) => {
          // Fillter the array
          this.posts = data.items; //This is an array with the content. No feed, no info about author etc..
          // const posts = res.filter((item) => item.categories.length > 0); // That's the main trick* !
        });
    },
    toText: function (node) {
      let tag = document.createElement("div");
      tag.innerHTML = node;
      node = tag.innerText;
      return node;
    },

    shortenText: function (text, startingPoint, maxLength) {
      return text.length > maxLength
        ? text.slice(startingPoint, maxLength)
        : text;
    },
  },
};
</script>

<style lang="stylus"></style>